.payment{
    background-color: white;
}
.payment_container h1{
    font-family: inherit;
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234,237,237);
    border-bottom: 1px solid lightgray;
}
.payment_section{
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}
.payment_title{
    flex:0.2;
}
.payment_address,.payment_items,.payment_details{
    flex: 0.8;
}

.payment_items .checkoutProduct{
    box-shadow: none;
}
.payment_items .checkoutProduct button{
    margin-top: 40px;
}