.subtotal{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 500px;
    height: 100px;
    padding: 20px;
    background-color: #f3f3f3;
    border: 1px solid #dddddd;
    border-radius: 3px;
}
.subtotal_gift{
    display: flex;
    align-items: center;
}
.subtotal button{
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29 ;
    color: #111;
    cursor: pointer;
}
@media only screen and (max-width: 1500px) {
    .subtotal{
    width: 300px;
    height: 100px;
    }
}
@media only screen and (max-width: 800px) {
    .subtotal{
    width: 100px;
    height: 150px;
    }
}